function getdoubleDigestNumer(number: number) {
    return ("0" + number).slice(-2)
}
function gettripleDigestNumer(number: number) {
    return ("0" + number).slice(-3)
}
export function writeUserData(degX: string, degY: string, degZ: string) {
    const now = new Date();
    const Month = now.getMonth() + 1;
    const date = now.getDate();
    const Hour = now.getHours();
    const Min = now.getMinutes();
    const Sec = now.getSeconds();
    const Milli = now.getMilliseconds();
    let db :Database;
    // eslint-disable-next-line no-restricted-globals
    if (location.hostname === "localhost") {
       db = getDatabase();
        // Point to the RTDB emulator running on localhost.
        connectDatabaseEmulator(db, "localhost", 9000);
      } 
      else{
         db = getDatabase(app);
      }
    const nowTime = Hour + ':' + Min + ':' + getdoubleDigestNumer(Sec) + ':' + gettripleDigestNumer(Milli);
    set(ref(db, 'users/' + Month + '/' + date + '/' + id + '/' + nowTime),
        {
            degs: [
                {
                    degX: degX,
                    degY: degY,
                    degZ: degZ
                }
            ]

        });
}