import React, { useState, useEffect, useRef, RefObject } from "react";
import Unity, { UnityContext } from "react-unity-webgl";
import { writeUserData } from "./FireBase/RealtimeDatabase"
import SoraApp from "./TeleXRSora"
const unityContext = new UnityContext({
  loaderUrl: "Build/Web.loader.js",
  dataUrl: "Build/Web.data.unityweb",
  frameworkUrl: "Build/Web.framework.js.unityweb",
  codeUrl: "Build/Web.wasm.unityweb",
});

function App() {
  const [isLoaded, setIsLoaded] = useState(false);
  let LeftVideoRef = useRef<HTMLVideoElement>(null);
  useEffect(function () {
    unityContext.on("SendAngleData", (angX, angY, angZ) => {
      writeUserData(angX, angY, angZ);
    });
    unityContext.on("loaded", function () {
      setIsLoaded(true);
    });
  }, []);
  const RightVideoRef = useRef<HTMLVideoElement>(null);
  return (
    <>
      <div style={{ textAlign: "center" }}>
        <Unity
          style={
            {
              visibility: isLoaded ? "visible" : "hidden",
              height: 690,
              width: 960,
              textAlign: "center",
            }}
          unityContext={unityContext}
        />
      </div>

    </>
  );
  //<SoraApp canvas={unityContext.htmlCanvasElement}></SoraApp>
}
export default App;